  outline: auto;
}

/* Tables */
/* ============================================ */
/**
1. Correct table border color inheritance in all Chrome and Safari.
*/
table {
  border-color: inherit; /* 1 */
  border-collapse: collapse;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.l-header {
  position: relative;
}
@media (max-width: 768px) {
  .l-header {
    height: 4.125rem;
  }
}
.l-header__wrapper {
  padding: clamp(1rem, 0.6192893401rem + 1.6243654822vw, 2rem) 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header__logo {
  margin-left: 3rem;
}
@media (max-width: 768px) {
  .l-header__logo {
    margin-left: 0;
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-60%, -50%);
  }
}
.l-header__info {
  display: flex;
  align-items: center;
}
.l-header__search-wrapper {
  overflow: hidden;
  margin-right: 2rem;
}
@media (max-width: 768px) {
  .l-header__search-wrapper {
    margin-right: 0;
  }
}
.l-header__search-box {
  transform: translateX(190px);
  transition: transform 0.3s;
}
.l-header__search-box.active {
  transform: translateX(0px);
}
.l-header__search-input {
  width: 8.125rem;
  border-bottom: 1px solid #000;
}
.l-header__search-btn {
  color: var(--base-color);
  background-color: #000;
  font-size: 0.75rem;
  padding: 0.3125rem 0.75rem;
}
.l-header__search-icon {
  cursor: pointer;
  margin-right: 0.5rem;
}
.l-header__account {
  padding: 1rem;
}
@media (max-width: 768px) {
  .l-header__account {
    padding: 0;
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translate(30%, -50%);
  }
}
.l-header__account-image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.l-header__account-image img {
  margin: 0 auto;
}
.l-header__account-caption {
  white-space: nowrap;
  font-size: clamp(0.625rem, 0.5774111675rem + 0.2030456853vw, 0.75rem);
  font-weight: 500;
  margin-top: clamp(0.375rem, 0.1370558376rem + 1.0152284264vw, 1rem);
}
.l-header__nav {
  padding: 1.5rem 0;
  border-top: 1px solid #000;
}
@media (max-width: 768px) {
  .l-header__nav {
    visibility: hidden;
    position: fixed;
    top: 2.5rem;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100vh;
    border: none;
    opacity: 0;
    transition: 0.3s;
  }
}
.l-header__nav.active {
  opacity: 1;
  visibility: visible;
}
.l-header__nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1140px;
  margin: 0 auto;
}
.l-header__nav-item {
  position: relative;
  text-align: center;
  flex: 1 1 auto;
}
.l-header__nav-item:nth-child(3), .l-header__nav-item:nth-child(6) {
  position: relative;
}
.l-header__nav-item:nth-child(3) .l-header__nav-link::after, .l-header__nav-item:nth-child(6) .l-header__nav-link::after {
  position: absolute;
  top: 60%;
  right: -1.5rem;
  transform: translateY(-60%);
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 7px solid #555555;
  border-bottom: 0;
  transition: 0.3s;
}
.l-header__nav-item:nth-child(3) .l-header__nav-link:hover::after, .l-header__nav-item:nth-child(6) .l-header__nav-link:hover::after {
  border-top-color: var(--main-color);
}
.l-header__nav-link {
  cursor: pointer;
  position: relative;
}
.l-header__nav-link::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--main-color);
  bottom: -3px;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.3s;
}
.l-header__nav-link:hover::before {
  transform: scale(1, 1);
}
.l-header__nav-inner-list {
  visibility: hidden;
  white-space: nowrap;
  position: absolute;
  top: 120%;
  left: 50%;
  transform: translateX(-50%);
  text-align: left;
  padding: 1.25rem;
  background-color: var(--base-color);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  opacity: 0;
  transition: 0.3s;
}
.l-header__nav-item:hover > .l-header__nav-inner-list {
  visibility: visible;
  opacity: 1;
}
.l-header__nav-sp-list {
  display: flex;
  flex-direction: column;
  background-color: var(--base-color);
  border: 1px solid #000;
}
.l-header__nav-sp-item {
  border-bottom: 1px solid #000;
}
.l-header__nav-sp-link {
  display: inline-block;
  padding: 0.5rem 0 0.5rem 0.5rem;
}
.l-header__nav-sp-inner-list {
  background-color: rgb(248, 222, 183);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}
.l-header__nav-sp-inner-link {
  display: inline-block;
}
.l-header__nav-sp-inner-link:nth-child(1) {
  padding: 0.5rem 0 0.5rem 0.5rem;
}
.l-header .has-sub-items {
  position: relative;
  width: 100%;
  text-align: left;
  padding-right: 2rem; /* プラスアイコンのスペース確保 */
}
.l-header .sub-item-icon {
  position: absolute;
  top: 50%;
  right: 0.5rem;
  transform: translateY(-50%);
  display: inline-block;
  vertical-align: middle;
  color: #333;
  line-height: 1;
  width: 0.7em; /* アイコンの幅を広く設定 */
  height: 0.07em; /* アイコンの高さを広げる */
  background: currentColor;
  border-radius: 0.07em; /* アイコンの丸みを少し増やす */
}
.l-header .sub-item-icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  transform: translate(-50%, -50%) rotate(90deg);
}
.l-header .sub-item-icon.active::before {
  display: none;
}

.l-footer {
  position: relative;
  background-image: url(../img/footer-bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: clamp(22.375rem, 21.1852791878rem + 5.076142132vw, 25.5rem);
  box-sizing: border-box;
}
.l-footer__wrapper {
  padding-top: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .l-footer__wrapper {
    flex-direction: column;
  }
}
.l-footer__footer-info {
  margin-top: 1rem;
}
.l-footer__nav {
  padding-top: 3.375rem;
}
.l-footer__nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .l-footer__nav-list {
    flex-direction: column;
    align-items: flex-start; /* 縦方向上揃え */
    justify-content: flex-start;
    text-align: left;
  }
}
.l-footer__nav-item:not(:first-of-type) {
  margin-left: 2rem;
}
@media (max-width: 768px) {
  .l-footer__nav-item:not(:first-of-type) {
    margin: 0.5rem 0 0 0;
  }
}
.l-footer__nav-link {
  font-size: clamp(0.875rem, 0.8274111675rem + 0.2030456853vw, 1rem);
}
.l-footer__copy {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
}

.l-container {
  max-width: 71.25rem;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .l-container {
    padding: 0 1.25rem;
  }
}

.l-pc-only {
  display: block;
}
@media (max-width: 768px) {
  .l-pc-only {
    display: none;
  }
}

.l-sp-only {
  display: none;
}
@media (max-width: 768px) {
  .l-sp-only {
    display: block;
  }
}

.c-tab-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: clamp(2.5rem, 1.524428934rem + 4.1624365482vw, 5.0625rem);
}

.c-tab-btn {
  display: inline-block;
  width: 20.3125rem;
  padding: 1.25rem 0;
  text-align: center;
  font-size: 1.125rem;
  font-weight: bold;
  box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s;
}
.c-tab-btn:not(:first-child) {
  margin-left: 1rem;
}
.c-tab-btn:hover {
  transform: translateY(4px);
  box-shadow: none;
}
.c-tab-btn:nth-child(2) {
  background-color: var(--main-color);
  color: var(--base-color);
}
.c-tab-btn:nth-child(3) {
  background-color: var(--accent-color);
  color: var(--base-color);
}

.c-tab-btn--sp {
  position: relative;
  display: inline-block;
  width: 6.25rem;
  height: 3.125rem;
  padding: 0.625rem 0;
  background-color: var(--base-color);
  box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s;
}
.c-tab-btn--sp:hover {
  transform: translateY(4px);
  box-shadow: none;
}
.c-tab-btn--sp img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.c-tab-btn--sp:not(:first-child) {
  margin-left: 1rem;
}
.c-tab-btn--sp:nth-child(2) {
  background-color: var(--main-color);
}
.c-tab-btn--sp:nth-child(3) {
  background-color: var(--accent-color);
}

.c-btn--submit {
  display: inline-block;
  text-align: center;
  font-size: 1.25rem;
  padding: 1.875rem 0;
  width: 17.5rem;
  border-radius: 3.125rem;
  background-color: var(--accent-color);
  color: var(--base-color);
  cursor: pointer;
  transition: 0.3s;
}
.c-btn--submit:hover {
  background-color: var(--base-color);
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
}

.c-menu-btn {
  position: fixed;
  top: 3%;
  left: 2.5rem;
  z-index: 10;
  width: 1.875rem;
  height: 1.875rem;
}
.c-menu-btn span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.5rem;
  height: 0.0625rem;
  background-color: #000;
  transition: 0.3s;
}
.c-menu-btn span:nth-child(1) {
  top: calc(50% - 8px); /* 8px上に移動 */
}
.c-menu-btn span:nth-child(3) {
  top: calc(50% + 8px); /* 8px上に移動 */
}
.c-menu-btn.active span:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.c-menu-btn.active span:nth-child(2) {
  opacity: 0;
}
.c-menu-btn.active span:nth-child(3) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.c-section-title {
  font-size: clamp(1.25rem, 0.9644670051rem + 1.2182741117vw, 2rem);
  font-weight: bold;
  text-align: center;
}

.c-toast {
  visibility: hidden;
  min-width: 15.625rem;
  background-color: var(--accent-color);
  color: var(--base-color);
  text-align: center;
  border-radius: 2px;
  padding: 1rem;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
  transform: translateX(-50%);
}
.c-toast.active {
  visibility: visible;
  animation: fadeIn 0.5s, fadeOut 0.5s 2.5s;
}

@keyframes fadeIn {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 1.875rem;
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    bottom: 1.875rem;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
.c-modal {
  display: none;
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  transition: opacity 0.5s;
}
.c-modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.c-modal__image {
  position: relative;
}
.c-modal__close-icon {
  position: absolute;
  top: -5%;
  right: -5%;
  width: clamp(1.875rem, 1.3991116751rem + 2.0304568528vw, 3.125rem);
  height: clamp(1.875rem, 1.3991116751rem + 2.0304568528vw, 3.125rem);
  cursor: pointer;
}

.p-course {
  position: relative;
  z-index: 3;
}

.swiper-container {
  position: relative;
  margin-top: clamp(3.5rem, 3.1192893401rem + 1.6243654822vw, 4.5rem);
  width: 100%; /* コンテナ幅を100% */
}

.swiper {
  position: relative;
  max-width: 1140px;
  margin: 0 auto;
  user-select: none;
}

.swiper-slide {
  display: flex;
  justify-content: center; /* 中央揃え */
}

.swiper-slide img {
  max-width: 100%; /* 画像がコンテナ内で縮小 */
  height: auto; /* アスペクト比を維持 */
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent; /* 背景色を透明にする */
  border: none; /* 枠線を削除 */
  border-radius: 50%;
  cursor: pointer;
  width: clamp(1.875rem, 0.4473350254rem + 6.0913705584vw, 5.625rem); /* ボタンの幅 */
  height: clamp(1.875rem, 0.4473350254rem + 6.0913705584vw, 5.625rem); /* ボタンの高さ */
}
.swiper-button-prev::after,
.swiper-button-next::after {
  content: ""; /* 擬似要素の内容を空にする */
  display: none;
}

.swiper-button-next {
  background-image: url(/src/img/slide-btn-next.png);
  background-size: contain; /* アイコンのサイズを調整 */
  background-repeat: no-repeat;
  background-position: center; /* アイコンを中央に配置 */
}

.swiper-button-prev {
  background-image: url(/src/img/slide-btn-prev.png);
  background-size: contain; /* アイコンのサイズを調整 */
  background-repeat: no-repeat;
  background-position: center; /* アイコンを中央に配置 */
}

.p-hero {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: clamp(12.5rem, 2.9822335025rem + 40.6091370558vw, 37.5rem);
  animation: image_anime 8s infinite ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes image_anime {
  0% {
    background-image: url("../img/hero-banner1.png");
  }
  33.33% {
    background-image: url("../img/hero-banner2.png");
  }
  66.66% {
    background-image: url("../img/hero-banner3.png");
  }
  100% {
    background-image: url("../img/hero-banner1.png");
  }
}
.p-trainer {
  margin-bottom: 17.75rem;
}
.p-trainer__card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3.375rem;
}
@media (max-width: 768px) {
  .p-trainer__card-wrapper {
    flex-direction: column;
    margin-top: 4.5rem;
  }
}
.p-trainer__card {
  position: relative;
  height: 24.25rem; /* 高さを設定 */
}
.p-trainer__card:not(:first-child) {
  margin-left: 2.5rem;
}
@media (max-width: 768px) {
  .p-trainer__card:not(:first-child) {
    margin: 18.75rem 0 0 0;
  }
}
.p-trainer__card-image {
  cursor: pointer;
}
.p-trainer__card-image:hover > .p-trainer__caption {
  visibility: visible;
  opacity: 1;
}
.p-trainer__caption {
  visibility: hidden;
  position: absolute;
  top: 90%;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  padding: 1.5rem;
  border-radius: 1.25rem;
  width: 16.5625rem;
  height: 17.75rem;
  opacity: 0;
  transform: 0.3s;
  background-color: var(--base-color);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.p-trainer .orange-bg {
  color: var(--base-color);
  background-color: var(--main-color);
}
.p-trainer .green-bg {
  color: var(--base-color);
  background-color: var(--accent-color);
}
.p-trainer__name {
  font-size: 1.25rem;
  text-align: center;
  margin-bottom: 0.9375rem;
}

.p-price__tab-content {
  display: none;
  margin-top: clamp(4rem, 3.5954949239rem + 1.7258883249vw, 5.0625rem);
}
.p-price__tab-content.active {
  display: block;
}
.p-price__table {
  max-width: 63.0625rem;
  margin: 0 auto;
  width: 100%;
  height: auto;
  border-collapse: collapse;
}
.p-price__table-th, .p-price__table-td {
  font-size: 1.25rem;
  align-items: center;
  border: 1px solid #000;
  text-align: center;
  padding: 1.5rem 2rem;
  font-weight: bold;
}
.p-price__table-th:nth-child(2) {
  background-color: var(--main-color);
  color: var(--base-color);
}
.p-price__table-th:nth-child(3) {
  background-color: var(--accent-color);
  color: var(--base-color);
}
.p-price__table-td:nth-child(2) {
  color: var(--main-color);
}
.p-price__table-td:nth-child(3) {
  color: var(--accent-color);
}
.p-price__table-sp {
  width: 100%;
  margin: 0 auto;
  border: 1px solid #000;
}
.p-price__table-sp-th, .p-price__table-sp-td {
  font-size: 0.875rem;
  width: 30%;
  padding: 1.5rem 0.625rem;
  border: 1px solid #000;
  text-align: center;
}
.p-price__content {
  display: block;
  max-height: 0;
  padding-bottom: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding-bottom 0.3s ease-out;
}
.p-price__content.open {
  max-height: 1000px;
  padding-bottom: 1rem;
}
.p-price__price-cell {
  background-color: var(--main-color);
  color: var(--base-color);
}
.p-price__benefit-cell {
  background-color: var(--accent-color);
  color: var(--base-color);
}
.p-price__table-sp-td[data-label=料金] ~ .p-price__table-sp-td[data-label=単回], .p-price__table-sp-td[data-label=料金] ~ .p-price__table-sp-td[data-label=月額], .p-price__table-sp-td[data-label=料金] ~ .p-price__table-sp-td[data-label=年間] {
  color: var(--main-color);
  font-weight: bold;
}
.p-price__table-sp-td[data-label=特典] ~ .p-price__table-sp-td[data-label=月額], .p-price__table-sp-td[data-label=特典] ~ .p-price__table-sp-td[data-label=年間] {
  color: var(--accent-color);
  font-weight: bold;
}

.p-review {
  position: relative;
  z-index: 3;
}
.p-review #carousel .swiper-button-prev,
.p-review #carousel .swiper-button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent; /* 背景色を透明にする */
  border: none; /* 枠線を削除 */
  border-radius: 50%;
  cursor: pointer;
  width: clamp(1.875rem, 0.4473350254rem + 6.0913705584vw, 5.625rem); /* ボタンの幅 */
  height: clamp(1.875rem, 0.4473350254rem + 6.0913705584vw, 5.625rem); /* ボタンの高さ */
}
.p-review #carousel .swiper-button-prev::after,
.p-review #carousel .swiper-button-next::after {
  content: ""; /* 擬似要素の内容を空にする */
  display: none; /* 擬似要素を非表示にする */
}
.p-review #carousel .swiper-button-next {
  background-image: url(/src/img/carousel-btn--next.png);
  background-size: contain; /* アイコンのサイズを調整 */
  background-repeat: no-repeat; /* アイコンの繰り返しを無効にする */
  background-position: center; /* アイコンを中央に配置 */
}
.p-review #carousel .swiper-button-prev {
  background-image: url(/src/img/carousel-btn--prev.png);
  background-size: contain; /* アイコンのサイズを調整 */
  background-repeat: no-repeat; /* アイコンの繰り返しを無効にする */
  background-position: center; /* アイコンを中央に配置 */
}

.p-accordion {
  margin-top: 4rem;
}
.p-accordion__item {
  border-bottom: 1px solid #B7B7B7;
}
.p-accordion__item:not(:first-child) {
  margin-top: 1rem;
}
.p-accordion__btn {
  position: relative;
  width: 100%;
  text-align: left;
  font-size: clamp(0.875rem, 0.8274111675rem + 0.2030456853vw, 1rem);
  padding: 0 1rem 1rem 0;
  align-items: flex-start;
  white-space: nowrap;
}
.p-accordion__plus-icon {
  position: absolute;
  top: 40%;
  right: 0.5rem; /* 右端に配置 */
  transform: translateY(-40%);
  display: inline-block;
  vertical-align: middle;
  color: #333;
  line-height: 1;
  width: 0.7em; /* アイコンの幅を広く設定 */
  height: 0.07em; /* アイコンの高さを広げる */
  background: currentColor;
  border-radius: 0.07em; /* アイコンの丸みを少し増やす */
}
.p-accordion__plus-icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  transform: translate(-50%, -50%) rotate(90deg);
}
.p-accordion__plus-icon.active::before {
  display: none;
}
.p-accordion__content {
  display: flex;
  align-items: flex-start;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s;
}
.p-accordion__content p {
  font-size: clamp(0.75rem, 0.654822335rem + 0.4060913706vw, 1rem);
  padding-bottom: 1rem;
}
.p-accordion__question {
  color: var(--accent-color);
  margin-right: 1.5rem;
  font-size: clamp(1rem, 0.904822335rem + 0.4060913706vw, 1.25rem);
  font-weight: bold;
}
@media (max-width: 768px) {
  .p-accordion__question {
    margin-right: 1rem;
  }
}
.p-accordion__answer {
  color: var(--main-color);
  margin-right: 1.5rem;
  font-size: clamp(1rem, 0.904822335rem + 0.4060913706vw, 1.25rem);
  font-weight: bold;
}
@media (max-width: 768px) {
  .p-accordion__answer {
    margin-right: 1rem;
  }
}

.p-contact__form-body {
  max-width: 63.25rem;
  margin: 0 auto;
  width: 100%;
}
.p-contact__form {
  margin-top: clamp(4rem, 3.8096446701rem + 0.8121827411vw, 4.5rem);
  border: 1px solid #000;
}
.p-contact__form-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #000;
}
@media (max-width: 768px) {
