}
@media (max-width: 768px) {
  .p-contact__form-input-wrapper {
    margin-left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.p-contact__form-input--postal {
  width: 9.8125rem;
  border: 1px solid #000;
