.p-contact__form-input--postal:nth-child(1) {
  margin: 0 1rem;
}
.p-contact__form-input--postal:nth-child(2) {
  margin-left: 1rem;
}
.p-contact__form-select {
  width: 11.3125rem;
  border: 1px solid #000;
  border-radius: 0.3125rem;
